import React from 'react';
import Nav from './nav';
import { withRouter } from 'react-router-dom'

class Header extends React.Component
{
	constructor(props)
	{
		super(props);
		this.onLogout = this.onLogout.bind(this);
		this.onHide = this.onHide.bind(this);
		this.state={
			isRefresh:false,
			isHide:false
		}
		//window.location.reload();
	}
	onHide=()=>{
		this.setState(pr=>({isHide:!pr.isHide}));
		return true;
	}

	onLogout=(e)=>{
		localStorage.setItem('rememberMe', false);
		localStorage.setItem('user', '');
		this.props.history.push('/');
	}
	componentDidMount()
	{
		global.mobno = localStorage.getItem('user');
		//alert(localStorage.getItem('user'))
		//this.loadPage();
	}

	LoadOnce() 
	{ 
		window.location.reload(); 
	} 

	render()
	{
		return(
			<div id="header">
      		<div class={this.state.isHide?"app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header closed-sidebar sidebar-mobile-open":"app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header"}>

	        <Nav visible={{display:'block'}} x={this.onHide} y={this.state.isHide} />
			<div className="app-main">
			<div className="app-sidebar sidebar-shadow">
			<div className="app-header__logo">

			<div className="header__pane ml-auto">
			<div>
			<button type="button" className="hamburger close-sidebar-btn hamburger--elastic" data-class="closed-sidebar">
			<span className="hamburger-box">
			<span className="hamburger-inner"></span>
			</span>
			</button>
			</div>
			</div>
			</div>
			<div className="app-header__mobile-menu">
			<div>
			<button type="button" className="hamburger hamburger--elastic mobile-toggle-nav">
			<span className="hamburger-box">
			<span className="hamburger-inner"></span>
			</span>
			</button>
			</div>
			</div>

			<div className="scrollbar-sidebar">
			<div className="app-sidebar__inner">
			<ul className="vertical-nav-menu">
			<li className="app-sidebar__heading">My Bookings</li>
			<li>
			<a href="/mybooking">
			<i className="metismenu-icon pe-7s-plane"></i>
			New Booking
			</a>
			</li>
			<li>
			<a href="index.html" className="mm-active">
			<i className="metismenu-icon pe-7s-rocket"></i>
			Booking List
			</a>
			</li>
			<li className="app-sidebar__heading">Reports</li>
			<li>
			<a href="#">
			<i className="metismenu-icon pe-7s-diamond"></i>
			Booking History
			<i className="metismenu-state-icon pe-7s-angle-down caret-left"></i>
			</a>
			</li>

			<li className="app-sidebar__heading">Settings</li>
			<li>
			<a href="/" onClick={this.onLogout}>
			<i className="metismenu-icon pe-7s-key"></i>
			Logout
			</a>
			</li>

			</ul>
			</div>
			</div>
			</div>    

			<div className="app-main__outer">
			<div className="app-main__inner">
			<div className="app-page-title">
			<div className="page-title-wrapper">
			<div className="page-title-heading">
			<div className="page-title-icon">
			<i className="metismenu-icon pe-7s-car">
			</i>
			</div>
			<div>{this.props.panelheader}
			<div className="page-title-subheading">{this.props.panelbody}
			</div>
			</div>
			</div>

			</div>
			</div>
			{this.props.render}  
			</div>
			</div>


			</div>
			</div>
			</div>

			);
	}
}

export default withRouter(Header);
